.container{display:flex;flex-direction:row;justify-content:center;align-items:center;gap:20px;width:100%;padding:20px}.contentWrapper{display:flex;flex-direction:row;justify-content:space-between;align-items:center;gap:0;width:100%;max-width:1200px}.imageStackWrapper{position:relative;width:100%;max-width:716px;margin:0 auto;padding-top:80px}.image1,.image2,.image3{position:absolute;top:0;left:0;transition:all .3s ease-in-out}.image1{z-index:3;width:400px}.image2{z-index:2;width:450px;transform:translate(10px, 10px)}.image3{z-index:1;width:500px;transform:translate(-15px, -50px)}.textContainer{width:50%;padding-left:20px}.title,.subtitle,.steps,.button{margin-bottom:20px}.title{font-size:48px;font-weight:700;line-height:56px}.title.light{color:var(--on-surface)}.title.dark{color:var(--surface)}.subtitle{font-size:20px;font-weight:400;line-height:28px}.subtitle.light{color:var(--secondary)}.subtitle.dark{color:var(--secondary2)}.stepTitle{display:flex;flex-direction:row;justify-content:flex-start;align-items:center;gap:8px;font-size:24px;font-weight:700;line-height:32px}.stepTitle.light{color:var(--on-surface)}.stepTitle.dark{color:var(--surface)}.stepDesc{font-size:16px;font-weight:400;line-height:24px}.stepDesc.light{color:var(--secondary)}.stepDesc.dark{color:var(--secondary2)}.button{background-color:var(--primary)}